.grid {
  margin: 100px 0 0; 
  margin-top: 20px; 
  margin-bottom: 10px; 
}

.navBar{
  margin-bottom: 30px; 
  background-color: #333;
  overflow: hidden;
  padding-bottom: 10px;
}
.navBar a {
  float: left;
  font-size: 22px;
  color: white;
  text-align: center;
  padding: 14px 22px;
  text-decoration: none;
  cursor: hand;
}

.navBar a:hover{
 color: rgb(159, 226 ,191);
}

.navBar .onlyLeft {

  margin-right: 780px;
  font-size: 28px;
}

.dropDown {
  float: left;
  overflow: hidden;
}

.dropDown .dropBtn{
  float: left;
  color: white;
  text-align: center;
  padding: 14px 22px;
  font-size: 22px;
  border: none;
  background-color: #333333;
}

.dropdown-algo {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  margin-top: 47px;
  text-align: left;
  padding: 14px 22px;
  font-size: 17px;
}
 
.dropdown-algo a {
  float: none;
  color: black;
  padding: 18px 26px;
  text-decoration: none;
  display: block;
  text-align: left;
  cursor: hand;
  border:none;
}


.dropdown-algo a:hover{
   background-color:  rgb(94, 94, 116);
   color: black;
}

.dropDown:hover .dropdown-algo {
  display: block;
}

.wall {
  float: left;
  overflow: hidden;
  border: none;
}

.wall .dropWall{
  float: left;
  color: white;
  text-align: center;
  padding: 14px 22px;
  font-size: 22px;
  border: none;
  background-color: #333333;
}

.dropdown-wall {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  margin-top: 47px;
}
 
.dropdown-wall a {
  float: none;
  color: black;
  padding: 3px 5px;
  text-decoration: none;
  display: block;
  text-align: left;
  cursor: pointer;
  font-size: 15px;
} 

.dropdown-wall a:hover{
  background-color:  rgb(94, 94, 116);
   color: black;
}



.wall:hover .dropdown-wall{
  display: block;
  cursor: hand;
}

.algoBar{
  float: none;
  color: black;
  padding: 3px 5px;
  text-decoration: none;
  display: block;
  text-align: left;
  cursor: pointer;
  font-size: 15px;
  font-style: bold;
}

.algoBar:hover{
  background-color:  rgb(94, 94, 116);
   color: black;
}

.dataContainer{
  border:black;
  border-width: 5px;
  margin: 5px;
}


table, th, td {
  border-collapse: collapse; 
 }
 
 table {
   display: none;
   text-transform:uppercase;
   margin-left: 120px;
   margin-top: 50px; 
   margin-bottom: 10px;
   border:1px solid #EEEEEE;
   width: 800px;
 }
 
 th {
   background-color: #333;
   color: #f1f1f1;
   text-align:center;
 }
 
 table tr:nth-child(even){
   background-color: #b9b2b2;
 }
 
 table tr:nth-child(odd){
   background:#EEEEEE;
 }
 
 #time0, #time1, #time2, #time3 {
   text-transform: lowercase;
 }
 
 #name0, #name1, #name2, #name3 {
   font-weight: bold;
 }

.newTools
{
  margin-top: -10px;
  color: white;
  text-align: center;
  padding: 14px 22px;
  font-size: 22px;
  border: none;
  background-color: #333333;
  margin-left: -20px;  
 cursor: pointer;
  
}
  
.newTools :hover
{
  background-color: rgb(141, 126, 126);
  padding: 10px 20px;
}

#currentAlgo {
  text-transform: uppercase;
  font-weight: bold;
}

#efficientSName, #efficientTName {
  font-weight: bold;
}

#efficientT, #efficientS {
  font-weight: bold;
  text-transform: uppercase;
}

ul, li{
  list-style:none;
  list-style-type:none;
}

ul {
  display: none;
  background: #333;
  padding: 20px;
  color: white;
  margin-left: 20px;
}

ul li {
  background: #b9b2b2;
  margin: 5px;
  color: #333;
  text-transform: uppercase;
  font-weight: bold;
}

#analysisContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

#timelist, #spacelist {
  font-weight: bold;
}